import Parse from 'parse';

export class Factory extends Parse.Object {
    constructor() {
        // Pass the ClassName to the Parse.Object constructor
        super('Factory');
        // All other initialization
        //名称
        this._name = 'Rawr';
        //编号
        this.nu = 'Rawr';
        //简称
        this.n = 'Rawr';
        //行业
        this.hy = '';
        //地址
        this.po = '';
        //联系人名称
        this.cn = '';
        //联系人电话
        this.cp = '';
        //备注
        this.des = '';

    }
     spawn(param) {
      this.set("nu", param.nu);
      this.set("name", param.n);
      this.set("n", param.n);
      this.set("hy", param.hy);
      this.set("po", param.po);
      this.set("cn", param.vn);
      this.set("des", param.des);
    }
}

